@font-face {
  font-family: "Inter";
  src: url("./inter/subset-Inter-Black.eot");
  src: url("./inter/subset-Inter-Black.woff2") format("woff2"),
    url("./inter/subset-Inter-Black.woff") format("woff"),
    url("./inter/subset-Inter-Black.ttf") format("truetype"),
    url("./inter/subset-Inter-Black.svg#Inter-Black") format("svg");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

